.wrapper {
  margin-top: 84px;
  margin-bottom: 124px;
  display: flex;
  column-gap: 480px;
  justify-content: center;
}

.header {
  font-weight: 400;
  font-size: 48px;
  line-height: 58px;
  margin-bottom: 30px;
}

.highlighted {
  font-weight: 900;
  text-transform: uppercase;
  background: linear-gradient(
    to right,
    rgba(252, 176, 69, 1) 0%,
    rgba(255, 63, 120, 1) 100%
  );
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subheading {
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

.redball {
  position: absolute;
  width: 311px;
  height: 311px;
  left: -102px;
  top: 251px;
  background: #961a1a;
  opacity: 0.5;
  filter: blur(196px);
}

@media (max-width: 480px) {
  .wrapper {
    margin-top: 40px;
    flex-direction: column;
    align-items: center;
    column-gap: 0px;
    margin-bottom: 50px;
  }
  .subheading {
    margin-bottom: 45px;
  }

  .header {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 15px;
  }

  .redball {
    display: none;
  }
}
