.header {
  display: flex;
  justify-content: center;
  margin-top: 60px;
}

.logoWrapper {
  max-width: 164px;
  margin-right: 20px;
}

.caption {
  font-family: "Montserrat";
  font-weight: 300;
  font-size: 10px;
}

.menu {
  display: flex;
  column-gap: 30px;
  margin-right: 174px;
}

.contacts {
  display: flex;
}

.contacts > li > div {
  width: 16px;
  height: 16px;
  margin-left: 35px;
}

.contacts__number {
  margin-right: 10px;
}

.icon1 {
  background-image: url(../img/tg-ico.svg);
  background-repeat: no-repeat;
}

.icon2 {
  background-image: url(../img/phone.svg);
  background-repeat: no-repeat;

}

.icon3 {
  background-image: url(../img/whats-ico.svg);
  background-repeat: no-repeat;
}

@media (max-width: 480px) {

  .header {
    margin-top: 0;
  }
  
  .logoWrapper {
    display: none;
  }

  .contacts {
    display: none;
  }

  .menu {
    align-items: center;
    margin-right: 0;
    column-gap: 15px;
    background-color: rgb(0, 0, 0);
    height: 60px;
  }

  .menu__link {
    text-transform: uppercase;
    font-size: 12px;
  }

  .menu__link:last-child {
    display: none;
  }
}
